import React, { Component } from "react";
import axios from "axios";

import configData from "./config.json"

const { Provider, Consumer } = React.createContext();

class AppContext extends Component {
    state = {
        currentId: "",
        currentLogin: "",
        displayObjURI: "",
    }

    swipeRequest = (direction) => {
        axios.get(configData.SWIPE_URI+direction, )
            .then((response) => {
                this.state.currentId = response.data.Id
                this.setState({displayObjURI: configData.LOAD_IMG_URI + response.data.Id})
                this.setState({currentLogin: response.data.Login})
            })
            .catch((err) => {
                console.log(err);
                console.log(configData.SWIPE_URI);
            });
    }

    updateExtension = (extension) => {
        let data = JSON.stringify({
            "extension": extension,
        })
        // Send a POST request
        return axios({
            method: 'post',
            url: configData.UPDATE_EXTENSIONS_URI,
            data: data,
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"
            }
        }).then((response) => {
            this.swipeRequest("")
            return (response.status)
        })
            .catch((error) => {
                return 500
            })}

    deleteObject = () => {
        let data = JSON.stringify({
            "objectId": this.state.currentId,
        })
        console.log(data)
        // Send a POST request
        axios({
            method: 'post',
            url: configData.DELETE_OBJ_URI,
            data: data,
            headers: {
                "Content-Type": "application/json"
            }
        }).then((response) => {
            this.swipeRequest("")
            console.log(response.data)
        })
            .catch((error) => {
                console.log(error.response)
            })}


    render() {
        return (
            <Provider
                value={{ displayObjURI: this.state.displayObjURI,
                    currentId: this.state.currentId,
                    currentLogin: this.state.currentLogin,
                    swipeRequest: this.swipeRequest,
                    updateExtension: this.updateExtension,
                    deleteObject: this.deleteObject,
                }}
            >
                {this.props.children}
            </Provider>
        );
    }
}

export { AppContext, Consumer as AppContextConsumer };
